.letsTalkSegment {
  width: 100%;
  height: 200px;
  background: #019ed3; }
  .letsTalkSegment .mainTalkDiv {
    display: flex;
    flex-direction: row;
    padding: 5%;
    width: 100%;
    height: 100%;
    max-width: 850px;
    margin: auto; }
    .letsTalkSegment .mainTalkDiv .letsTalkLeft {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 50%;
      height: 100%;
      border: red; }
    .letsTalkSegment .mainTalkDiv .letsTalkRight {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 50%;
      height: 100%;
      border: white; }
      .letsTalkSegment .mainTalkDiv .letsTalkRight .talkCTA {
        display: flex;
        align-items: center;
        opacity: 0.9;
        height: 100%;
        width: none;
        color: #ffffff;
        font-size: 5rem;
        padding: 0;
        transition: all 350ms ease-in-out;
        font-weight: 500; }
        .letsTalkSegment .mainTalkDiv .letsTalkRight .talkCTA:hover {
          opacity: 0.3; }
    @media (max-width: 550px) {
      .letsTalkSegment .mainTalkDiv {
        flex-direction: column; }
        .letsTalkSegment .mainTalkDiv .letsTalkLeft {
          width: 100%;
          height: 50%; }
          .letsTalkSegment .mainTalkDiv .letsTalkLeft .talkHead {
            width: inherit; }
        .letsTalkSegment .mainTalkDiv .letsTalkRight {
          width: 100%;
          height: 50%; }
          .letsTalkSegment .mainTalkDiv .letsTalkRight .talkCTA {
            align-self: flex-start; } }
